<template>
  <div>
    <!-- Overlay -->
    <div
      @click="$emit('close-dialog')"
      class="absolute inset-0 bg-black z-9000 opacity-25"
    ></div>
    <!-- Input Form -->
    <div class="absolute inset-0 z-9010 flex justify-center items-center rounded">
      <div class="w-1/2 p-4 bg-tan">
        <h2 v-if="!attrProps.id" class="mb-4 text-2xl">Add an Attraction</h2>
        <h2 v-else class="mb-4 text-2xl">Edit an Attraction</h2>
        <label class="block mb-4">
          <span class="text-gray-700">Name</span>
          <input
            v-model="name"
            type="text"
            class="form-input mt-1 block w-full"
            placeholder="Attraction"
          >
        </label>
        <div class="flex justify-between">
          <label class="block mb-4">
            <span class="text-gray-700">Lattitude</span>
            <input
              v-model="lat"
              type="text"
              class="form-input mt-1 block w-full"
              placeholder="Attraction"
            >
          </label>
          <label class="block mb-4">
            <span class="text-gray-700">Longitude</span>
            <input
              v-model="lng"
              type="text"
              class="form-input mt-1 block w-full"
              placeholder="Attraction"
            >
          </label>
        </div>
        <label class="block mb-4">
          <span class="text-gray-700">Category</span>
          <select v-model="category" class="form-select block w-full mt-1">
            <option disabled value="">Nothing selected</option>
            <option value="Park">Park</option>
            <option value="Museum">Museum</option>
            <option value="Place">Place</option>
            <option value="Neighborhood">Neighborhood</option>
            <option value="Pueblo Magico">Pueblo Magico</option>
          </select>
        </label>
        <label class="block mb-4">
          <span class="text-gray-700">Wikipedia Link</span>
          <input
            v-model="web"
            type="text"
            class="form-input mt-1 block w-full"
            placeholder="https://..."
          >
        </label>
        <label class="block mb-4">
          <span class="text-gray-700">Image</span>
          <input
            v-model="image"
            type="text"
            class="form-input mt-1 block w-full"
            placeholder="Faked Upload"
          >
        </label>
        <div class="my-6 text-center">
          <button
            v-if="!attrProps.id"
            @click="saveAttraction"
            class="px-4 py-1 ml-6 text-white bg-blue-600 hover:bg-blue-700 uppercase tracking-wider rounded "
          >Submit</button>
          <button
            v-else
            @click="updateAttraction"
            class="px-4 py-1 ml-6 text-white bg-blue-600 hover:bg-blue-700 uppercase tracking-wider rounded "
          >Submit</button>
          <button
            @click="$emit('close-dialog')"
            class="px-4 py-1 ml-6 text-white bg-red-600 hover:bg-red-700 uppercase tracking-wider rounded "
          >Cancel</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Imports
import mainService from '@/services/mainService'

// Export component instance
export default {
  name: "NewAttractionDialog",

  props: {
    attrGeom: {
      type: Object,
      required: true
    },
    attrProps: {
      type: Object,
      default: function () {
        return {
          category: ''
        }
      }
    }
  },

  data () {
    return {
      name     : this.attrProps.name,
      category : this.attrProps.category,
      web      : this.attrProps.web,
      image    : this.attrProps.image,
      lat      : Math.round( 100000 * this.attrGeom.lat) / 100000,
      lng      : Math.round( 100000 * this.attrGeom.lng) / 100000
    }
  },

  methods: {
    async updateAttraction () {
      try {
        const response = (await mainService.updateAttraction({
          id       : this.attrProps.id,
          name     : this.name,
          category : this.category,
          image    : this.image,
          web      : this.web,
          lat      : +this.lat,
          lng      : +this.lng
        })).data

        if (response.success) {
          this.$emit ('update-feature', {
            type: "Feature",
            properties: {
              id       : this.attrProps.id,
              name     : this.name,
              category : this.category,
              image    : this.image,
              web      : this.web,
            },
            geometry: {
              type: "Point",
              coordinates: [this.lng, this.lat]
            }
          })
        }
      } catch (error) {
        alert(error.message)
      }
    },
    async saveAttraction () {
      try {
        const response = (await mainService.saveAttraction({
          name     : this.name,
          category : this.category,
          image    : this.image,
          web      : this.web,
          lat      : +this.lat,
          lng      : +this.lng
        })).data

        if (response.success) {
          this.$emit ('add-feature', {
            type: "Feature",
            properties: {
              id       : response.success.id,
              name     : this.name,
              category : this.category,
              image    : this.image,
              web      : this.web,
            },
            geometry: {
              type: "Point",
              coordinates: [this.lng, this.lat]
            }
          })
        }
      } catch (error) {
        alert(error.message)
      }
    }
  }

}
</script>
